.menu-container {
   position: absolute;
   top: 0;
   width: 100%;
   padding: 0 20px;
   background-color: black;
   right: -100%;
   height: 100vh;
   color: white;

   transition: right 0.2s ease-in-out;
}

.menu-container.active {
   right: 0;
}

.menu-container-header {
   height: 150px;
   display: flex;
   align-items: center;
   justify-content: space-between;
}

.menu-item {
   list-style: none;
}

.menu-item a {
   text-decoration: none;
   font-size: 32px;
   font-family: "Josefin Sans", sans-serif;
   line-height: 50px;
   text-transform: uppercase;
   color: hsl(0, 0%, 55%);
   transition: color 0.3s ease-in-out;
}

.menu-item a:hover {
   color: hsl(0, 0%, 100%);
}
