.nav {
   height: 150px;
}

.nav .container {
   height: 100%;
   display: flex;
   align-items: center;
   justify-content: space-between;
   color: hsl(0, 0%, 100%);
}

.nav-logo {
   font-size: 24px;
   cursor: pointer;
}

.list-container {
   display: flex;
   align-items: center;
}

.list-container .menu {
   display: none;
}

.nav-list {
   display: flex;
   align-items: center;
   gap: 20px;
   font-family: "Josefin Sans", sans-serif;
   font-weight: bold;
}

.list-item {
   position: relative;
   list-style: none;
   padding: 0 10px;
   cursor: pointer;
}

.list-item::after {
   position: absolute;
   opacity: 0;
   content: "";
   left: 0;
   right: 0;
   bottom: -10px;
   margin: 0 auto;
   width: 30%;
   height: 2px;
   background: white;
   transition: all 0.3s ease-in-out;
}

.nav-link {
   text-decoration: none;
   color: rgba(255, 255, 255, 0.8);
   transition: all 0.3s ease-in-out;
}

.list-item:hover .nav-link {
   color: rgb(255, 255, 255, 1);
}

.list-item:hover::after {
   opacity: 1;
}

@media only screen and (max-width: 768px) {
   .list-container .menu {
      display: block;
   }

   .nav-list {
      display: none;
   }
}
